/* ==================================================
   Section Portfolio
================================================== */

.type-work {
  color: #FFFFFF;
  font-size: 24px;
  font-weight: 300;
  margin-bottom: $mediumSpacing;
  line-height: 1em;
  text-transform: uppercase;
}

.work-nav #filters {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap;

   @media #{$medium-and-up} {
    display: block;
   }
}

.work-nav #filters li {

  padding: 0;
  margin: $xsmallSpacing $mediumSpacing $xsmallSpacing 0;
  display: inline-block;

  &:last-child{
    margin-right: 0;
  }

  @media #{$medium-and-up} {
    margin: 0 0 $xxlargeSpacing 0;
     display: block;
  }
}

.work-nav #filters li a {
  color: #7F8289;
  font-size: 16px;
}

.work-nav #filters li a:hover {
  color: #FFFFFF;
}

.work-nav #filters li a.selected {
  color: $mainTheme;
}






.work__container{

  display: flex;
  flex-direction: column;
  
  padding: 0 $largeSpacing;

  @media #{$x-small-and-up} {
    padding: 0 $largeSpacing*2;
  }

  @media #{$medium-and-up} {
      flex-direction: row;
      padding: 0 $xxlargeSpacing;
  }

  @media #{$large-and-up} {
      padding: 0 $xxlargeSpacing*2;
  }
}

.work-categories__wrapper{

  flex-direction: column;

  @media #{$small-and-up} {
    display: flex;
    flex: 1;
  }
}

.work-items__wrapper {

  @media #{$medium-and-up} {
    display: flex;
    flex: 3;
    flex-flow: row wrap;
  }
}

.work__item {

  width: 100%;
  padding: $largeSpacing;

  .work__item--description{ background-color: rgba(65,120,190, 1); }

  a:hover{ text-decoration: none; }

  @media #{$x-small-and-up} {
    width: 46%;
    padding:$xxsmallSpacing;
  }

  @media #{$x-large-and-up} {
    width: 30%;
  }


}
