/*/*!
* Name: variable.scss
* Description: reusable global variables;
*//*!*/

/***************Media Query sizes***************/
$xx-small-screen-up: 425px !default;
$x-small-screen-up: 601px !default;
$small-screen-up: 769px !default;
$medium-screen-up: 993px !default;
$large-screen-up: 1201px !default;
$xx-small-screen: 424px !default;
$x-small-screen: 600px !default;
$small-screen: 768px !default;
$medium-screen: 992px !default;
$large-screen: 1200px !default;
$x-large-screen: 1600px !default;
$xx-large-screen: 2000px !default;

$x-small-and-up: "only screen and (min-width: #{$xx-small-screen-up})" !default;
$small-and-up: "only screen and (min-width : #{$x-small-screen-up})" !default;
$medium-and-up: "only screen and (min-width : #{$small-screen-up})" !default;
$large-and-up: "only screen and (min-width : #{$medium-screen-up})" !default;
$x-large-and-up: "only screen and (min-width : #{$large-screen})" !default;
$xx-large-and-up: "only screen and (min-width : #{$x-large-screen})" !default;
$xxx-large-and-up: "only screen and (min-width : #{$xx-large-screen})" !default;
$x-large-and-down: "only screen and (max-width : #{$x-large-screen})" !default;
$large-and-down: "only screen and (max-width : #{$large-screen})" !default;
$small-and-down: "only screen and (max-width : #{$small-screen})" !default;
$small-and-up: "only screen and (min-width : #{$small-screen})" !default;
$medium-and-down: "only screen and (max-width : #{$medium-screen})" !default;
$x-small-and-down: "only screen and (max-width : #{$x-small-screen})" !default;
$large-only: "only screen and (min-width : #{$medium-screen-up}) and (max-width : #{$large-screen})" !default;
$medium-only: "only screen and (min-width : #{$small-screen-up}) and (max-width : #{$medium-screen})" !default;
$small-only: "only screen and (min-width : #{$x-small-screen-up}) and (max-width : #{$small-screen})" !default;


// Spacings
$xxsmallSpacing: 8px;
$xsmallSpacing: 12px;
$smallSpacing: 16px;
$mediumSpacing: 20px;
$largeSpacing: 24px;
$xlargeSpacing: 28px;
$xxlargeSpacing: 32px;

$menuHeight: 50px;

//Colors
$mainTheme: #00BFFF;
$mainThemeHover: #0099cc;
