/*Global styles*/
body, h1, h2{
  font-family: "Courier New", Courier, monospace;
  font-weight: lighter;
  margin: 0;
  padding: 0;
}

body{
	background: color('gray',80) url('../images/layout/bg.png') repeat left top;
}

a{
	color: color('blue',30);
	text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

a.selected {
  color: color('blue',30);
}

/*------------------------------------------------------
                     Fancy box
--------------------------------------------------------*/

.fancybox-outer .fancybox-nav{
  width: 36px;
  height: 34px;
  top: calc(50% - 17px);

  span{
    opacity: 0.5;
    visibility: visible;
    transition: opacity .3s ease-in-out;

    &:hover{
       opacity: 1;
    }
  }
}



// ---------------------------------------------------------
//                     Lazy Image Loading
// -----------------------------------------------------------

.lazyload,
.lazyloading {
    opacity: 0;
}
.lazyloaded {
    opacity: 1;
    transition: opacity 3s;
}
