.navbar {
	font-size: 18px;
	background-color: #343334;
	z-index: 999;
    
    &.stuck{
    	position: fixed;
    	width: 100%;
    	top:0;
        
        .logo-container{
            .menu-item__link {
                width: 40px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                margin-top: 0;
                
                .btop--text{
                    fill: white;
                }
                
                .btop--background{
                    fill: color('gray',90);
                    transition: fill 0.3s;
                }
                
                &:hover {
                    .btop--background {
                        fill: $mainTheme;
                    }
                }
            }
        }
    }

	.logo-container {
		display: flex;
		justify-content: space-between;
		padding: $xsmallSpacing/2 $xsmallSpacing;
        
        .menu-item__link {
            width: 0;
            margin-top: -50px;
            transition: margin-top 0.5s;
        }

		.logo-label {
			display: block;
			width: 40px;
			height: 40px;
			cursor: pointer;
		}

		.logo-label:after {
			content: '';
			display: block;
			width: 30px;
			height: 5px;
			background-color: white;
			margin: 7px 5px;
			box-shadow: 0 10px 0 white, 0 20px 0 white;
			transition: all 0.3s;
		}

		.logo-label:hover:after {
			background-color: $mainTheme;
			box-shadow: 0 10px 0 $mainTheme, 0 20px 0 $mainTheme;
		}
	}

	#mobile-menu {
		display: none;
	}

	#mobile-menu:checked ~ .menu-container {
		max-height: 250px;
	}

	.menu-container {
		max-height: 0;
		transition: max-height 0.5s;
		overflow: hidden;
		display: block;
		z-index: 9999;
		text-align: right;
		display: flex;
		flex-direction: column;
	}

	.menu-container .menu-item {
		display: block;
		width: 100%;
		text-align: left;
		text-align: center;
		border-top: solid thin #777;
		transition: background-color 0.3s;

		.menu-item__link {
			color: white;
			transition: color 0.3s;
			display: block;
			text-decoration: none;
			padding: $mediumSpacing 0;
			text-transform: capitalize;
		}

		&:hover {
			background-color: #272727;

			.menu-item__link {
				color: $mainTheme;
			}
		}
	}


	/********************************************
					Mobile Menu
	*********************************************/

	@media #{$medium-and-up} {
		padding: $xsmallSpacing;
		display: flex;
		justify-content: space-between;
		align-items: center;
		height: $menuHeight;

		.logo-container {
			.logo {
				line-height: inherit;
				display: inline-table;
				padding: $xsmallSpacing;
			}

			.logo-label {
				display: none;
			}
		}

		.menu-container {
			max-height: none;
			flex-direction: row;
			display: inline-block;
			line-height: 40px;
		}

		.menu-container .menu-item {
			text-align: center;
			display: inline;
			margin: 0;
			border: none;
			float: left;
			width: 100px;
			margin: 0 $smallSpacing;

			.menu-item__link {
				cursor: pointer;
				outline: none;
				width: 100%;
				text-decoration: none;
				padding: 0;
				display: block;
			}

			&:hover {
				cursor: pointer;
				position: relative;
				background-color: inherit;

				.menu-item__link {
					color: $mainTheme;
				}
			}
		}
	}
}
