.contact__container{

    padding: 0 $xxlargeSpacing;
    width: 100%;
    max-width: 700px;
    margin: 0 auto $xxlargeSpacing*2 auto;

    @media #{$x-small-and-up} {
         padding: 0 $xxlargeSpacing*2;

    }

    .contact__label{
        color: $mainTheme;
        font-size: 18px;
        text-transform: capitalize;
        padding: $xsmallSpacing 0;
    }

    /* Style inputs with type="text", select elements and textareas */
    input[type=text], select, textarea {
        width: 100%; 
        padding: 12px; 
        border: 1px solid #ccc; 
        border-radius: 4px; 
        box-sizing: border-box; 
        margin-top: 6px; 
        margin-bottom: 16px; 
        resize: vertical;
        display: block; 
    }

    /* Style the submit button with a specific background color etc */
    input[type=submit] {
        background-color: $mainTheme;
        color: white;
        padding: 12px 20px;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        margin-top: $smallSpacing;
        transition: all 0.3s;

        &:hover{
            background-color: $mainThemeHover;
        }
    }

    #message{
        height: 200px;
    }

}