/* ==================================================
   Section Life
================================================== */

.life__items{

  padding: 0 $largeSpacing;

  @media #{$x-small-and-up} {
    padding: 0 $largeSpacing*2;
  }

  @media #{$medium-and-up} {
      padding: 0 $xxlargeSpacing;
      display: flex;
      justify-content: center;
      text-align: center;
  }

  @media #{$large-and-up} {
      padding: 0 $xxlargeSpacing*2;
  }
}

.life__item{

  &:nth-child(2){
    margin: $xxlargeSpacing*2 0;
  }

  @media #{$medium-and-up} {

    width: 30%;
    text-align: left;

    &:nth-child(2){
      margin: 0 5%;
    }
  }

}

.life__item--title{
  color: color('gray',10);
  font-size: 24px;
  text-transform: capitalize;
  padding: $xsmallSpacing 0;

  @media #{$medium-and-up} {
    padding: $largeSpacing 0;
  }
}

.life__item--description{
  color: color('gray',40);
  line-height: 28px;
}


.life_item__image__container{
    border: 5px solid #fff;
    box-shadow: 2px 2px 0px color('cool-gray',90);
}

/*==================================================
                  image overlay
  ==================================================*/

.mask_container{
  text-align: center;
  overflow: hidden;
  position: relative;

  &:hover {
    .mask { opacity: 1; }
    .mask__title, .mask__description { opacity: 1; transform: translateY(0px); }
    .mask__image { transform: scale(1.1); }
    .mask__description { transition-delay: 0.1s; }
    .mask__link { transition-delay: 0.4s; opacity: 1;}
  }
}

.mask {

    width: 100%;
    height: 100%;
    position: absolute;
    overflow: hidden;
    top: 0;
    left: 0;

    //specific animation
    display: flex;
    flex-direction: column;
    justify-content: center;
    opacity: 0;
    background-color: rgba(90,170,250, 0.7);
    transition: all 0.4s ease-in-out;

}

.mask__image {
    display: block;

    //specific animation
    transition: all 0.2s linear;
}

.mask__title {
    text-transform: uppercase;
    color: #fff;
    text-align: center;
    position: relative;
    font-size: 20px;
    padding: 10px;
    background: rgba(0, 0, 0, 0.8);
    margin: 20px 0 0 0;

    //specific animation
    transform: translateY(-100px);
    opacity: 0;
    transition: all 0.2s ease-in-out;
}

.mask__description {
    font-family: Georgia, serif;
    font-style: italic;
    font-size: 20px;
    position: relative;
    color: #fff;
    padding: 10px 20px 20px;
    text-align: center;

    //specific animation
    transform: translateY(200px);
    opacity: 0;
    transition: all 0.3s linear;
}
.mask__link {
    display: inline-block;
    text-decoration: none;
    padding: 7px 14px;
    background: #000;
    color: #fff;
    text-transform: uppercase;
    box-shadow: 0 0 1px #000;
    width: 80%;
    max-width: 160px;
    margin: 0 auto;


    //specific animation
    opacity: 0;
    transition: all 0.3s ease-in-out;
}

.mask__link:hover {
    box-shadow: 0 0 5px #000;
}
