/* Header section */
.intro{
  text-align: center;
  height: calc(100vh - #{$menuHeight});
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: $mediumSpacing*2 $xxlargeSpacing;
}

.background.lazyloaded{
  background: linear-gradient(rgba(10,10,10,0.8), rgba(10,10,10,0.8)),url("../images/slider-images/image01.png");
  background-size: cover;
	background-attachment: fixed;
}

.header-title{
  font-size: 50px;
  cursor: default;
  letter-spacing: -4px;

  @media #{$small-and-up} {
    font-size: 60px;
  }

  span{
    color: $mainTheme;
    font-size: 32px;
  }
}

.header-description{

  margin: $largeSpacing*3 auto 0 auto;
  text-align: left;

  li{
    display: block;
    text-indent: 0.4em;
    font-style: italic;
    letter-spacing: -1px;
    font-size: 20px;
    line-height: 1.5em;

  }


  li:before {

    content: "• ";
    color: $mainTheme;
    vertical-align: middle;
  }

  @media #{$medium-and-up} {

    text-align: center;

    li{
      display: inline-block;
    }

    li:first-child:before{
        content: "";
      }

  }

}

.header-sub{
  margin: $largeSpacing*3 auto 0 auto;
  max-width: 600px;
}
