/* ==================================================
   Case Studies
================================================== */
.caseStudies{
    padding: 0 $largeSpacing $largeSpacing $largeSpacing;

    @media #{$x-small-and-up} {
      padding: 0 $largeSpacing*2 $largeSpacing*2 $largeSpacing*2;
    }

    @media #{$medium-and-up} {
        padding: 0 $xxlargeSpacing $xxlargeSpacing $xxlargeSpacing;
    }

    @media #{$large-and-up} {
        padding: 0 $xxlargeSpacing*2 $xxlargeSpacing*2 $xxlargeSpacing*2;
    }
    
    .title-description{
        margin-bottom: $smallSpacing;
        color: white;
    }
}
/* Style the buttons that are used to open and close the accordion panel */
.accordion {
    background-color: #eee;
    color: #444;
    cursor: pointer;
    padding: 18px;
    width: 100%;
    text-align: left;
    border: none;
    outline: none;
    transition: 0.4s;
}

/* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
.active, .accordion:hover {
    background-color: #ccc;
}

/* Style the accordion panel. Note: hidden by default */
.panel {
    background-color: transparent;
    color: color('gray',30);
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.2s ease-out;
    display: flex;
    flex-direction: column;
    &:last-child{
        border-bottom: solid thin;
    }
    
    .caseStudies__details{
        padding: $smallSpacing;
        overflow-y: scroll;
        min-height: 200px;

        &--text:not(:last-child){
            margin-bottom: $smallSpacing;
        }

        &--list{
            padding-left: $xlargeSpacing;
            
            li:not(:last-child){
                margin-bottom: $xxsmallSpacing;
            }
        }
        
        list-style-type: circle
    }

    .caseStudies__slideshow{
        position: relative;
        margin-top: $smallSpacing;
        
        .mySlides1,
        .mySlides2{
            width: 100%;
        }
        
        .display-left-1,
        .display-right-1,
        .display-left-2,
        .display-right-2{
            position: absolute;
            top: calc(50% - 25px);
            display: flex;
            justify-content: center;
            align-items: center;
            width: 50px;
            height: 50px;
            opacity: 0.5;
            color: color('gray',80);
            border: none;
            background: color('gray',50);
            transition: opacity 0.5s;
            padding-top: $xxsmallSpacing;
            
            &:hover{
                opacity: 1;
            }
        }

        .display-left-1,
        .display-left-2{
            padding-right: $xsmallSpacing;
        }
        
        .display-right-1,
        .display-right-2{
            right: 0;
        }
    }
    
    @media #{$large-and-up} {
        flex-direction: row;
        
        .caseStudies__details{
            width: 80%;
        }
        
        .caseStudies__slideshow{
            margin-top: 0;
        }
    }
    
}

.accordion:after {
    content: '\02795'; /* Unicode character for "plus" sign (+) */
    font-size: 13px;
    color: #777;
    float: right;
    margin-left: 5px;
}

.active:after {
    content: "\2796"; /* Unicode character for "minus" sign (-) */
}
