.title-page{
	text-align: center;
	padding: $xlargeSpacing*2 $smallSpacing;
   
    color: color('white');

    @media #{$x-small-and-up} {
    	 padding: $xxlargeSpacing*2 $xlargeSpacing;

    }

    @media #{$small-and-up} {
    	 padding: $xxlargeSpacing*2 $xlargeSpacing*2;

    }

}

.title{
	font-size: 48px;
}

.title-description{
	margin-top: $xxlargeSpacing;
	font-size: 24px;
	line-height: 1.5em;
}