/* ==================================================
   Section Education
================================================== */
.sub-title{
    text-align: left;
    margin: $xxlargeSpacing 0;
}

.school__items{

  padding: 0 $smallSpacing;

  @media #{$x-small-and-up} {
    padding: 0 $xxlargeSpacing;
  }

  @media #{$medium-and-up} {
    display: flex;
    justify-content: center;
    text-align: center;
  }

  @media #{$large-and-up} {
      padding: 0 $xxlargeSpacing*2;
  }
}

.school__item{

  &:nth-child(2){
    margin: $xxlargeSpacing*2 0;
  }

  text-align: center;

  @media #{$medium-and-up} {

    width: 30%;

    &:nth-child(2){
      margin: 0 5%;
    }
  }

}

.school__item--title{
  color: color('gray',10);
  font-size: 24px;
  text-transform: capitalize;
  padding: $xsmallSpacing 0;

  @media #{$medium-and-up} {
      height: 96px;
      padding: $largeSpacing 0;
  }
}

.school__item--description{
  color: color('gray',40);
  line-height: 28px;
}


.school_item__image__container{
    width: 150px;
    height: 150px;
    position: relative;
    overflow: hidden;
    border-radius: 50%;
    margin: 0 auto $xsmallSpacing auto;

    @media #{$medium-and-up} {
        hmargin: 0 auto;
    }
}
